import Vue from 'vue';
import Router from 'vue-router';
import Start from './views/Start.vue';
import Storage from './utils/storage.js';

Vue.use(Router);

const VueRouter = new Router({
    // mode: 'history',
    routes: [
        {
            path: '/start',
            name: 'start',
            meta: { viewName: 'Sākums' },
            component: Start
        },
        {
            path: '/project',
            name: 'project',
            meta: { viewName: 'Projekts' },
            component: () =>
                import(/* webpackChunkName: "project" */ './views/Project.vue')
        },
        {
            path: '/photos',
            name: 'photos',
            meta: { viewName: 'Photos' },
            component: () =>
                import(/* webpackChunkName: "photos" */ './views/Photos.vue')
        },
        {
            path: '/container',
            name: 'container',
            meta: { viewName: 'Konteinera akts' },
            component: () =>
                import(
                    /* webpackChunkName: "container" */ './views/Container.vue'
                )
        },
        {
            path: '*',
            redirect: '/start'
        }
    ]
});

VueRouter.beforeEach((to, from, next) => {
    // 1 check if token exists
    // 2 do dropbox stuff
    // 3 after dropbox stuff extract token from the redirecte_from property
    // check - https://dropbox.github.io/dropbox-sdk-js/tutorial-Authentication.html
    if (!Storage.getDropboxToken()) {
        if (to.redirectedFrom) {
            const params = to.redirectedFrom.slice(1).split('&');
            const [tokenParamName, tokenValue] = params[0].split('=');

            if (tokenParamName === 'access_token') {
                Storage.saveDropboxToken(tokenValue);
            }
        }
    }

    next();
});

export default VueRouter;
