const fileReader = () => {
    const convertToBase64 = photo => {
        const fr = new FileReader();

        fr.readAsDataURL(photo);

        return new Promise((resolve, reject) => {
            fr.onloadend = event => {
                resolve(event.target.result);
            };
        });
    };

    return Object.freeze({ convertToBase64 });
};

export default fileReader();
