import { generateId } from './helpers.js';

const Storage = () => {
    const PHOTOS_KEY = 'ostas_foto';
    const DROPBOX_TOKEN_KEY = '';

    const _stringify = obj => JSON.stringify(obj);
    const _parse = str => JSON.parse(str);

    const getPhotos = () => {
        const photos = localStorage.getItem(PHOTOS_KEY);

        return photos ? _parse(photos) : [];
    };

    const savePhotos = async newPhotos => {
        const photos = getPhotos();
        const newPhotosWithId = newPhotos.map(photo => ({
            photo,
            id: generateId()
        }));

        if (photos) {
            localStorage.setItem(
                PHOTOS_KEY,
                _stringify([...photos, ...newPhotosWithId])
            );

            return [...photos, ...newPhotosWithId];
        }

        localStorage.setItem(PHOTOS_KEY, _stringify(newPhotosWithId));

        return newPhotosWithId;
    };

    const deletePhoto = id => {
        const photos = getPhotos();
        const newPhotos = photos.filter(photo => photo.id !== id);

        localStorage.setItem(PHOTOS_KEY, _stringify(newPhotos));

        return newPhotos;
    };

    const getDropboxToken = () => localStorage.getItem(DROPBOX_TOKEN_KEY);

    const saveDropboxToken = token =>
        localStorage.setItem(DROPBOX_TOKEN_KEY, token);

    return Object.freeze({
        getPhotos,
        savePhotos,
        deletePhoto,
        getDropboxToken,
        saveDropboxToken
    });
};

export default Storage();
