import Vue from 'vue';
import Vuex from 'vuex';
import Container from './container.js';
import { getClientName } from './storeHelpers.js';
import { getCurrentTime, generatePresentFilePath } from '../utils/helpers.js';
import FileReader from '../utils/fileReader.js';
import Storage from '../utils/storage.js';
import { Dropbox } from 'dropbox';

Vue.use(Vuex);

const Store = new Vuex.Store({
    state: {
        isLoading: false,
        project: {
            container: [],
            worksheet: {},
            photos: [],
            specefication: {}
        }
    },

    getters: {
        isLoading: state => state.isLoading,
        project: state => state.project,
        clientName: state => getClientName(state.project.container),
        container: state => state.project.container
    },

    mutations: {
        updateLoadingStatus(state, loadingStatus) {
            state.isLoading = loadingStatus;
        },
        updateContainer(state, newContainer) {
            state.project.container = newContainer;
        },
        updatePhotos(state, photos) {
            state.project.photos = photos;
        }
    },

    actions: {
        createNewContainer({ commit }) {
            const newContainer = Container.generateNewContainer();

            commit('updateContainer', newContainer);
        },
        updateContainer({ commit }, newContainer) {
            commit('updateContainer', newContainer);
        },
        async loadPhotos({ commit }) {
            const photos = await Storage.getPhotos();

            commit('updatePhotos', photos);
        },
        async savePhotos({ commit }, photos) {
            const base64Photos = Object.keys(photos).map(key =>
                FileReader.convertToBase64(photos[key])
            );

            const convertedPhotos = await Promise.all(base64Photos);
            const allPhotos = await Storage.savePhotos(convertedPhotos);

            commit('updatePhotos', allPhotos);
        },
        async deletePhoto({ commit }, photoId) {
            const photos = await Storage.deletePhoto(photoId);

            commit('updatePhotos', photos);
        },
        async uploadToDropbox({ state, commit }) {
            commit('updateLoadingStatus', true);

            const photos = Storage.getPhotos();
            if (photos.length < 1) {
                commit('updateLoadingStatus', false);

                return;
            }

            const dbx = new Dropbox({
                accessToken: Storage.getDropboxToken(),
                fetch
            });

            for (let i = 0; i < photos.length; i++) {
                const photoName = `${getCurrentTime()}_photo-${i + 1}`;
                const base64index = photos[i].photo.indexOf('base64,');
                const buffer = Buffer.from(
                    photos[i].photo.slice(base64index + 7),
                    'base64'
                );
                const file = new File([buffer], photoName);

                const response = await dbx.filesUpload({
                    path: `/Osta/${generatePresentFilePath()}/${photoName}.png`,
                    contents: file
                });
            }

            commit('updateLoadingStatus', false);
        }
    }
});

export default Store;
