<template>
    <div class="StartView">
        <Button @onClick="startNewProject">
          <span v-if="isAuthenticated">JAUNS</span>
          <span v-else>AUTORIZĒTIES</span>          
        </Button>
        <Button disabled>TURPINĀT</Button>
    </div>
</template>

<script>
import { Dropbox } from 'dropbox';
import Storage from '../utils/storage.js';

import Button from '../components/Button.vue';

export default {
    name: 'StartView',

    components: {
        Button
    },

    data() {
        return {
            isAuthenticated: Storage.getDropboxToken()
        }
    },

    methods: {
        startNewProject() {
            if (this.isAuthenticated) {
                this.$router.push('project')
            } else {
                this.authenticate()
            }
        },
        authenticate() {
            const dbx = new Dropbox({
                fetch,
                clientId: 'ok3llbmvpa5susm'
            });
            const dbUri = dbx.getAuthenticationUrl(window.location.origin);
            window.location.href = dbUri;
        }
    }
}
</script>

<style lang="scss" scoped>
.StartView {
    height: 100%;
    border: 2px solid purple;
    align-items: center;
    display: flex;
    justify-content: space-around;
}
</style>