const _generateCurrentTimeData = () => {
    const now = new Date();
    const timeString = now.toTimeString().substring(0, 8);
    const year = now.getFullYear();
    const month = now.getMonth();
    const day = now.getDate();

    return { year, month, day, timeString };
};

const getCurrentTime = () => _generateCurrentTimeData().timeString;

const getTimeStamp = () => {
    const { year, month, day, timeString } = _generateCurrentTimeData();

    // since months are starting with 0 index, adding + 1 for human readability
    return `${timeString} - ${day}/${month + 1}/${year}`;
};

const generatePresentFilePath = () => {
    const { year, month, day } = _generateCurrentTimeData();

    const monthNames = [
        'Janvaris',
        'Februaris',
        'Marts',
        'Aprilis',
        'Maijs',
        'Junijs',
        'Julijs',
        'Augusts',
        'Septembris',
        'Oktobris',
        'Novembris',
        'Decembris'
    ];

    return `${year}/${monthNames[month]}/${day}`;
};

const generateId = () =>
    Math.random()
        .toString()
        .substring(2, 10);

export { getTimeStamp, generateId, getCurrentTime, generatePresentFilePath };
