<template>
    <button
      class="Button"
      :class="{
          'Button--red': red,
          'Button--disabled': disabled || isLoading
      }"
      :disabled="disabled"
      @click="onClick"
    >
        <span v-if="isLoading">Lādējās...</span>
        <slot v-else />
    </button>
</template>

<script>
export default {
    name: 'Button',

    props: {
        disabled: {
            type: Boolean,
            default: false
        },
        red: {
            type: Boolean,
            default: false
        },
        isLoading: {
            type: Boolean,
            default: false
        }
    },

    methods: {
        onClick() {
            this.$emit('onClick')
        }
    }
}
</script>

<style lang="scss" scoped>
.Button {
    background-color: rgb(0, 200, 0);
    color: white;
    cursor: pointer;
    font-weight: 700;
    height: auto;
    padding: 20px 0;
    width: 130px;

    &--red {
        background-color: red;
    }

    &--disabled {
        background-color: rgb(180, 180, 180);
    }
}
</style>